/* following colors are named after the ui-kit in order to be used in functions or properties as it */
// stylelint-disable scss/dollar-variable-default
$smagenta1: #de2554;
$smagenta2: #b21f4a;
$sblue1: #26828e;
$sblue2: #166670;
$syellow1: #fbcd00;
$syellow2: #f9b800;
$sgray1: #333;
$gray1: $sgray1;
$sgray2: #eee;
$gray2: $sgray2;
$sgray3: #555;
$gray3: $sgray3;
$sgray4: #ccc;
$gray4: $sgray4;
$dark: $black;
$greige: $gray2;

$primary-official: $smagenta1;
$primary: $smagenta1;
$primary2: $syellow1;
$secondary: $sblue1;

// Colors
// stylelint-disable scss/dollar-variable-pattern -- variable name depends on Boosted
$red: #e70002; // Error
$bgRed: #fde5e6;
$yellow: #ffcd0b; // Warning
$bgYellow: #fffae7;
$green: #3de35a; // Success
$bgGreen: #ecfdef;
$blue: #26b2ff; // Info
$bgBlue: #e9f8ff;

$success: $green; // Success
$bgSuccess: $bgGreen;
$warning: $yellow; // Warning
$bgWarning: $bgYellow;
$danger: $red; // Danger
$bgDanger: $bgRed;
$info: $blue; // Info
$bgInfo: $bgBlue;
// stylelint-enable

$sosh-alert-button-colors: (
  "success": $bgSuccess,
  "info": $bgInfo,
  "warning": $bgYellow,
  "danger": $bgRed,
);

$theme-colors: ();
$sosh-theme-colors: map-merge(
  (
    "gray1": $sgray1,
    "sgray1": $sgray1,
    "gray2": $sgray2,
    "sgray2": $sgray2,
    "gray3": $sgray3,
    "sgray3": $sgray3,
    "gray4": $gray4,
    "sgray4": $sgray4,
    "smagenta1": $smagenta1,
    "smagenta2": $smagenta2,
    "sblue1": $sblue1,
    "sblue2": $sblue2,
    "syellow1": $syellow1,
    "syellow2": $syellow2,
    "primary": $primary,
    "primary2": $primary2,
    "secondary": $secondary,
    "primary-official": $primary-official,
    "success-light": $bgSuccess,
    "warning-light": $bgWarning,
    "danger-light": $bgDanger,
    "info-light": $bgInfo
  ),
  $theme-colors
);

$sosh-bg-colors-allowed: (
  "sgray1": $sgray1,
  "sgray2": $sgray2,
  "smagenta1": $smagenta1,
  "smagenta2": $smagenta2,
  "sblue1": $sblue1,
  "sblue2": $sblue2,
  "syellow1": $syellow1,
  "syellow2": $syellow2,
  "sdark": $dark
);

$sosh-color-allowed: ($white, $black, $primary, $secondary, $syellow1, $smagenta1, $smagenta2, $sblue1, $sblue2);

/* Button */
$btn-border-radius: 3px;
$btn-border-width: 2px;
$btn-padding-y: calc(.8125rem - 1px);
$btn-padding-x: calc(2.25rem - 1px);

/* Typography configuration for mobile (xs and sm) */
$h2-padding-bottom-mobile: px-to-rem(10);

/* Typography configuration for tablet and small desktop (md, lg) */
$h2-padding-bottom: px-to-rem(9);

/* widescreen variables aren't part of bootstrap, they are used in our custom scss */
$h2-padding-bottom-widescreen: px-to-rem(8);

// Tooltip
$tooltip-bg: $gray2 !default;

/* functional icons */
$functional-icons: ();

// Tables
$table-border-color: $gray2 !default;

// Dropdown
$dropdown-link-hover-color: $gray3 !default;
